





import Vue from 'vue';
import SvgIcon from '@/configurator/components/utils/SvgIcon.vue';
import BackButton from '@/common/components/BackButton.vue';
// @ts-ignore
import SvgArrowLeft from '@/common/svgs/arrow.svg';
import {UI_STATE_ACTIONS, UiState} from '@/configurator/store/ui-state';
import {UI_BUTTON} from '@/configurator/embedding/types';

export default Vue.extend({
  components: {
    SvgIcon,
    BackButton,
  },
  computed: {
    icons(): object {
      return {
        SvgArrowLeft,
      };
    },
  },
  methods: {
    backToWebsite() {
      if (this.$sdkConnector.hasPlanner) {
        this.$sdkConnector.goBack();
        return;
      }
      this.$embeddingCallbacks.onBackToWebsite();
      if ((this.$store.state.uiState as UiState).isExpandedForConfigure) {
        this.$store.dispatch(UI_STATE_ACTIONS.PAUSE_CONFIGURING);
      }
      this.$embeddingCallbacks.onButtonClicked(UI_BUTTON.PAUSECONFIGURE);
    },
  },

});
